@media screen and (max-width: 840px) {
    /* Обвертка навигации */
    .nav-wrapper {
        width: 200px;
        padding: 0px 0px 60px 0px;
        
        background-color: var(--bg-secondary);
        border-left: 1px solid var(--border-primary);

        position: fixed;
        top: 39px;
        right: 0;

        /* display: none; */
    }

    /* Навигация */
    nav {
        width: calc(100% - 8px - 8px); /* Растояние между контентом и скролом - 8px и слево - 8px */
        margin: 0 auto;
    }
}